body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-preview-img {
  margin-left: -8vw;
  margin-right: -8vw;
  padding-top: 2vh;
  padding-bottom: 2vh;
  max-width: 80vw;
}

.App-button {
  margin-top:.5em;
  width: 100%;
  height: 3em;
}

.App-back-link {
  color: white;
  font-size: 1em;
}

.App-back-link:visited {
  color: white;
}


.App-text-input {
  padding: .4em;
  /*
  font-size: $width/15;
  border-bottom: solid $width/150 rgba(white, .5);
  padding-top: $width/15;
   * */
}

body{
  background-color: #282c34;
}

.App-header {
  background-color: #282c34;
  min-height: 80vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-page {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  border: 1px solid lavender;
  min-height: 70vh;
  margin-left: 10vw;
  margin-right: 10vw;
  padding-left: 10vw;
  padding-right: 10vw;
  margin-top: 10vh;
  margin-bottom: 10vh;
}

.App-panel {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.App-link {
  color: #61dafb;
  cursor: pointer;
  text-decoration: none;
  border: 1px solid lavender;
  margin-left: 10vw;
  margin-right: 10vw;
  padding-top: 10vh;
  padding-bottom: 10vh;
}

.App-link-button{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  color: #61dafb;
  cursor: pointer;
  text-decoration: none;
  border: 1px solid lavender;
  padding-top: 5vh;
  padding-bottom: 5vh;
  margin-bottom: 3vh;
  font-size: calc(10px + 3vmin);
}


#app-info{
  color: lavender;
  font-size: .7em;
}

#input-hider{
  width: 0px; 
  height: 0px; 
  overflow: hidden;
}


